import React, {useRef, useState} from "react";
import 'bootstrap/dist/css/bootstrap.min.css';
import {Button, Col, Container, Form, Row} from "react-bootstrap";


export default function Modal() {
    let result = []

    const curriculum = require("./cs-2019.json").curriculum

    var grade = ["A","A-","B+","B","B-","C+","C","C-","D","F"]
    var term = ["1","2"]
    var year = ["2019","2020","2021","2022","2023"]
    var major = ["Computer Science","Information Technology"]

    const majorRef = useRef('Computer Science')
    const termRef = useRef('1');
    const yearRef = useRef('2023');
    const groupRef = useRef('Social Science Courses');
    const subjectRef = useRef('GE 2202');
    const gradeRef = useRef('B+');

    const [optionsOfSubject, setOptionsOfSubject] = useState([
        curriculum.subjects[0].subjects.map((subject, index) => {
            return(
                <option key={index} value={subject.code}>{`${subject.code} ${subject.name}`}</option>            
                )
    })
    ])

    const groupChanges = () => {
        const gpName = groupRef.current.value        
        let selectedSubjects = curriculum.subjects.find((group) => group.groupName === gpName).subjects        
        const SelectedSubjectOptions = selectedSubjects.map((subject, index) => {
            return(
                <option key={index} value={subject.code}>{`${subject.code} ${subject.name}`}</option>            
                )
        })
        setOptionsOfSubject(SelectedSubjectOptions)
    }

    const subjectList = curriculum.subjects.flatMap(
        (subject) => subject.subjects.map(
            (subject) => ({code: subject.code, name: subject.name})
        ))

    const matchCourse = (subjectCode) => {
        return subjectList.find((subject) => (subject.code === subjectCode)).name    
    }

    const addForm = () => {
        const formObj = {
            semester: termRef.current.value + '/' + yearRef.current.value,
            subject: subjectRef.current.value,
            subjectName: matchCourse(subjectRef.current.value),
            grade: gradeRef.current.value        }
                
        console.log(formObj)
        
    }
    
    return(
        <>
            <h1 className={"header"}>Grade Tracker</h1>
            <Form  className={"form"}>
            <Row>
                    <Col>
                        <Form.Group>
                            <Form.Label>Choose Major</Form.Label>
                            <Form.Select ref={majorRef}>
                                {
                                     major.map((major, index) => {
                                        return (
                                            <option key={index} value={major}>{major}</option>
                                        )
                                    })
                                
                                }
                            </Form.Select>
                            </Form.Group> 
                    </Col>
                </Row>
                <Row>
                    <Col>
                        <Form.Group>
                            <Form.Label>Term</Form.Label>
                            <Form.Select ref={termRef}>
                                {
                                     term.map((term, index) => {
                                        return (
                                            <option key={index} value={term}>{term}</option>
                                        )
                                    })
                                
                                }
                            </Form.Select>
                            </Form.Group> 
                    </Col>
                </Row>
                <Row>
                    <Col>
                    <Form.Group>
                        <Form.Label>Year</Form.Label>
                        <Form.Select ref={yearRef}>
                            {
                                year.map((year, index) => {
                                    return(
                                        <option key={index} value={year.toString()}>{year}</option>        
                                        )
                                })
                            }
                        </Form.Select>
                    </Form.Group>
                    </Col>
                </Row>
                <Form.Group>
                    <Form.Label>Group</Form.Label>
                    <Form.Select ref={groupRef} onChange={groupChanges}>
                        {
                            curriculum.subjects.map((group, index) => {
                                return(
                                    <option key={index} value={group.groupName}>{group.groupName}</option>        
                                    )
                            })
                        }
                    </Form.Select>
                </Form.Group>
                <Form.Group>
                    <Form.Label>Subjects</Form.Label>
                    <Form.Select ref={subjectRef} onChange={groupChanges}>
                        {
                            optionsOfSubject
                        }
                    </Form.Select>
                </Form.Group>
                <Form.Group>
                    <Form.Label>Grade</Form.Label>
                    <Form.Select ref={gradeRef}>
                        {
                            grade.map((grade, index) => {
                                return(
                                    <option key={index} value={grade.toString()}>{grade}</option>       
                                    )
                            })
                        }
                    </Form.Select>
                </Form.Group>
                <div>
                    <Button onClick={addForm} className={"button"}>
                        Add
                    </Button>
                </div>   
            </Form>
            <Container>
                <Col sm={3}>
                <h1></h1>
                {result.map((info, i) => {
                    return <Row key={i}>
                            <Col>{info.semester}</Col>
                            <Col>{info.subject}</Col>
                            <Col>{info.subjectName}</Col>
                            <Col>{info.grade}</Col>
                        </Row>
                
                    })}
                </Col>
            </Container>
        </>
    )

}